.rank-unavailable {
  display: flex;
  margin: 6px 0;
  border: 2px solid lightgray;
  border-radius: 5px;
  padding: 5px;
  opacity: 0.5;
}

.rank-available {
  display: flex;
  margin: 6px 0;
  border: 2px solid gray;
  border-radius: 5px;
  padding: 5px;
}

.rank-available-claimed {
  display: flex;
  margin: 6px 0;
  border: 2px solid black;
  background-color: rgb(38, 231, 31);
  border-radius: 5px;
  padding: 5px;
}

.lvl-required {
  font-weight: bold;
  min-width: 48px;
  text-align: start;
}

.rank-description {
  text-align: start;
}
